// Adapted from https://github.com/Heisenbergjs/heisenberg/blob/master/app/app.js
(function() {
  var Coop = {

    Modules: {},

    Events: $({}),

    init: function () {
      // Loop through all the modules added to the coop.Modules object. We check that the init function of each Module can be called.
      for(var x in Coop.Modules) {
        if (typeof Coop.Modules[x].init === 'function') {
          Coop.Modules[x].init();
        }
      }

      Coop.Events.trigger('render');
    }
  };

  // expose the Coop object globally
  window.Coop = Coop;
})();

(function() {
  'use strict';

  window.Coop = window.Coop || { Modules: {} };

  Coop.Modules.MenuToggle = {
    defaults: {
      toggleSelector: '.mobile-menu-toggle',
      navSelector: '#navigation',
      menuTextSelector: '.mobile-menu-text',
      closeText: 'Close',
      openClass: 'open'
    },
    init: function(options) {
      this.settings = $.extend({}, this.defaults, options);

      this.$toggle = $(this.settings.toggleSelector);
      this.$nav = $(this.settings.navSelector);
      this.$menuText = $(this.settings.menuTextSelector);

      this.originalText = this.$menuText.eq(0).text();

      this.bindEvents();
    },
    bindEvents: function() {
      var self = this;

      this.$toggle
        .off('click.MenuToggle')
        .on('click.MenuToggle', function(e) {
          e.preventDefault();
          self.toggle();
        });
    },
    toggle: function() {
      if (this.$toggle.hasClass(this.settings.openClass)) {
        this.close();
      }
      else {
        this.open();
      }
    },
    open: function() {
      this.$toggle.add(this.$nav).addClass(this.settings.openClass);
      $(this.settings.menuTextSelector, this.$toggle).text(this.settings.closeText);
    },
    close: function() {
      this.$toggle.add(this.$nav).removeClass(this.settings.openClass);
      $(this.settings.menuTextSelector, this.$toggle).text(this.originalText);
    }
  };
}());

(function() {
  'use strict';

  window.Coop = window.Coop || { Modules: {} };

  var Tabs = function($el, options) {
    this.init($el, options);
    return this;
  };

  Tabs.prototype = {
    init: function($el, options) {
      this.$tabs = $el;
      this.$navItems = $('.tabs-nav a', $el);
      this.$tabPanels = $('.tabs-content .tab-panel', $el);

      this.addAria();
      this.bindEvents();

      // Activate first tab
      this.activateTab(this.$navItems.eq(0).attr('href'));

      $el.addClass('init');
    },
    bindEvents: function() {
      var self = this;

      this.$navItems
        .off('click.Tabs')
        .on('click.Tabs', function(e) {
          e.preventDefault();
          self.activateTab($(this).attr('href'));
        });
    },
    addAria: function() {
      this.$navItems.each(function() {
        var controlId = $(this).attr('href').replace('#', '');
        $(this).attr('aria-controls', function(i, currentAttribute) {
          return currentAttribute ? currentAttribute + ' ' + controlId : controlId;
        });
      });
      this.$tabPanels.attr('aria-live', 'polite');
    },
    activateTab: function(tabId) {
      this.$navItems
        .removeClass('active')
        .filter('[href="' + tabId + '"]')
          .addClass('active');

      this.$tabPanels
        .removeClass('active')
        .attr({
          'aria-hidden': true,
          'hidden': true
        })
        .filter(tabId)
          .addClass('active')
          .removeAttr('aria-hidden hidden');
    }
  };

  Coop.Modules._Tabs = Tabs;

  Coop.Modules.Tabs = {
    init: function() {
      return $('.tabs').each(function() {
        $(this).data('Tabs', new Tabs($(this), $(this).data()));
      });
    }
  };
}());

